// UserProvider.server.tsx
import { createContext, ReactNode } from "react";
import { getRequest } from "../utils/axios.server"; // server-side fetch
import { ENDPOINTS } from "@/shared/site";

export const UserContext = createContext<any>(null);

export interface UserProps {
  id: number;
  first_name: string;
  last_name: string;
  email: string;
  role: "admin" | "superadmin" | "user";
  is_blocked: boolean;
  is_active: boolean;
}

interface UserProviderProps {
  children: ReactNode;
}

export default async function UserProvider({ children }: UserProviderProps) {
  let user: UserProps | null = null;

  try {
    const res = await getRequest({ url: ENDPOINTS.profile }); // fetch server-side
    user = res.data;
  } catch (err) {
    console.error("Failed to fetch user:", err);
  }

  // Server-side context (static)
  return (
    <UserContext.Provider value={{ user }}>{children}</UserContext.Provider>
  );
}
