import { ENDPOINTS } from "@/shared/site";
import { AxiosErrorProps } from "@/types";
import axios from "axios";
import { AxiosRequestConfig } from "axios";
import { cookies, headers } from "next/headers";
import { NextResponse } from "next/server";


interface AxiosProps {
    method?: string;
    url: string;
    data?: object | null;
    headers?: AxiosRequestConfig["headers"];
    withCredentials?: boolean;
    baseURL?: string;
}



const axiosInstance = axios.create();

const customRequest = async ({
    method = "get",
    url,
    headers = {},
    data = {},
    withCredentials = true,
    baseURL = ENDPOINTS.http_server_base
}: AxiosProps) => {
    const cookieStore = await cookies();

    const allCookies = cookieStore.getAll();
    const cookieHeader = allCookies
        .map((cookie) => `${cookie.name}=${cookie.value}`)
        .join("; ");

    const response = await axiosInstance({ method, url: url.endsWith("/") ? url.slice(0, -1) : url, data, headers: { ...headers, Cookie: cookieHeader }, withCredentials, baseURL });
    const next_response = NextResponse.json(response.data, { status: response.status });

    const set_cookies = response.headers["set-cookie"] as string[]


    if (set_cookies) {
        set_cookies.forEach((c) => {
            next_response.headers.append("Set-Cookie", c);
        });
    }

    return next_response
}


export const getRequest = async (params: AxiosProps) =>
    await customRequest(params);
export const postRequest = async (params: AxiosProps) =>
    await customRequest({ ...params, method: "post" });
export const putRequest = async (params: AxiosProps) =>
    await customRequest({ ...params, method: "put" });
export const patchRequest = async (params: AxiosProps) =>
    await customRequest({ ...params, method: "patch" });
export const deleteRequest = async (params: AxiosProps) =>
    await customRequest({ ...params, method: "delete" });


export const getError = (error: AxiosErrorProps) => {
    return {
        data: error.response?.data as any,
        status: error.response?.status,
    };
};
